import React, { useState, useRef } from 'react';
import ReactFlow,  { ReactFlowProvider, Background, Elements, removeElements, addEdge, Controls } from 'react-flow-renderer';
import {Menu} from './menu/Menu';
import {nodeTypes, lib} from './library/Library'
import {Encoders, Control} from './controls/Controls'
import {objectData, objectParam} from './library/LibraryTypes'
import { saveAs } from 'file-saver';
import {EditText} from 'react-edit-text';

let initialElements:Elements = [
  {
    id: '1',
    type: 'audioInNode',
    data: { name: "Audio In", params: [], inputs: [], outputs: [] },
    position: { x: 500, y: 100 },
  },
  {
    id: '2',
    type: 'audioOutNode',
    data: { name: "Audio Out", params: [], inputs: [], outputs: [] },
    position: { x: 500, y: 600 },
  }
];

let initialControls: Control[]=[
  {name:"ctl1", value:0},{name:"ctl2", value:8},{name:"ctl3", value:16},{name:"ctl4", value:64},
];



let id = 3;
let anyNull:any=null;
let selIndex= 0;
const seperator = "~%$^"

function App() {

  const getId= () => `dndnode_`+id++
  
  const reactFlowWrapper: any = useRef(null);
  const [reactFlowInstance, setReactFlowInstance]:any = useState(null);
  const [elements, setElements] = useState(initialElements);
  const [controls, setControls] = useState(initialControls);
  const [selObj, setSelectedObj] = useState(anyNull);
  const [auth, setAuth] = useState(false);

  const onElementClick = (event: any, element: any) => {}/*console.log('click', element)*/;
  const onPaneClick = (event: any) => {}/*console.log('onPaneClick', event)*/;
  const onConnect = (params: any) => setElements((els) =>  addEdge({ ...params, animated: true, style: { stroke: '#bbb', strokeWidth:'3' } }, els));
  const onElementsRemove = (elementsToRemove: any) => setElements((els) => removeElements(elementsToRemove, els));
  const onLoad = (_reactFlowInstance: any) => setReactFlowInstance(_reactFlowInstance);
  const onSelectionChange = (selements: Elements<any> | null) => {
    
    if (selements != null) {
      if(selements?.length === 1){
        elements.forEach((e, i) => {
          if(e.id === selements[0].id){
            setSelectedObj(e);
            selIndex = i;
          }
        })      
      } else{setSelectedObj(null);}
    } else{setSelectedObj(null);}
  };

  const onParamChange = (params:objectParam[]) => {
    const updatedEles = [...elements];
    updatedEles[selIndex].data.params = params;
    setElements(updatedEles);
  }

  const onControlChange=(allCtls:Control[], control: number, val: number, name: string) => {

    if(allCtls != null){
      setControls(allCtls);
      return;
    }

    if(control < controls.length){

    /* Make shallow copy */
    let ctls = [...controls];
    /* Update and pass new values to callback */
    if(val !== null){
      ctls[control].value = val;
    }
    if(name !== null){
      ctls[control].name = name;
    }
    
    setControls(ctls);
    
      setElements((els) =>
        els.map((el) => {
          if(el.data !== undefined){
            el.data.params.forEach((p,pi) => {
              if(p.fixed === false && p.ctlIdx === control){
                switch(p.mapShape){
                  case 0: //Linear
                    p.value = (((ctls[control].value/100) * (p.mapMax-p.mapMin)) + p.mapMin).toFixed(2);
                    break;
                  case 1: //Exp
                    p.value = (((ctls[control].value/100) * (ctls[control].value/100) * (p.mapMax-p.mapMin)) + p.mapMin).toFixed(2);
                    break;
                  case 2: //Parabola
                    let p1 = (ctls[control].value-50)/5;
                    p.value = ( (((100-(p1*p1))/100) * (p.mapMax-p.mapMin)) + p.mapMin).toFixed(2);
                    break;
                }
              }
            })
          }
          return el;
        })
      );
    }


  }

  const onDragOver = (event: any) => {
    event.preventDefault();
    event.dataTransfer.dropEffect = 'move';
  };
  
  const onDrop = (event: any) => {
    event.preventDefault();
    const reactFlowBounds = reactFlowWrapper.current.getBoundingClientRect();
      
    const type = event.dataTransfer.getData('application/reactflow');
    const position = reactFlowInstance.project({
        x: event.clientX - reactFlowBounds.left,
        y: event.clientY - reactFlowBounds.top,
      });
    
    /* Initialise new object data */
    var objectData: objectData = {
      name: `${type}`,
      params: [],
      inputs: [],
      outputs: [],
    }

    /* Add default params */
    lib.objects[type].paramList.forEach((p,i)=>{
      objectData.params.push({fixed: true, value:p.default, ctlIdx: 0, mapMin:0, mapMax:1, mapShape:0});
    })
    
    /* Create and add new object */
    const newNode = {
      id: getId(),
      type,
      position,
      data: objectData,
      selectable: true,
    };

    setElements((es) => es.concat(newNode));

  };

  const save = () => {
    if (reactFlowInstance) {
      const flow = reactFlowInstance.toObject();
      let blobby = new Blob([JSON.stringify(flow) + seperator + JSON.stringify(controls)] , {type: "text/plain;charset=utf-8"});
      saveAs.saveAs(blobby, "config.hwc")
    }
  }

  const load = (e) => {
    e.preventDefault();
    const reader = new FileReader();
    reader.onload = async (e) => { 
        let r:any = reader.result;
        let settings = r.split(seperator)
        let flow=JSON.parse(settings[0]);
        let cs=JSON.parse(settings[1]);
        setElements(flow.elements || []);
        setControls(cs)
        id = flow.elements.length;
    };
    reader.readAsText(e.target.files[0])
  };

  
  return (
    auth?
    <div style={{ height:"100vh" }}>
      
      <ReactFlowProvider>
        <div style={{ height:"100vh" }} className="reactflow-wrapper" ref={reactFlowWrapper}>
        <ReactFlow
          style={{background: '#171620'}}
          elements={elements}
          onElementClick={onElementClick}
          onSelectionChange={onSelectionChange}
          onPaneClick={onPaneClick}
          onElementsRemove={onElementsRemove}
          onConnect={onConnect}
          onLoad={onLoad}
          onDrop={onDrop}
          onDragOver={onDragOver}
          nodeTypes={nodeTypes}
          deleteKeyCode={46} /* 'delete'-key */
        >
          <Menu show={false} controls={controls} selObj={selObj} onParamChange={onParamChange} onSave={save} onLoad={load}/>
          <Background  gap={12} size={1} color={'#000000'}/>
          <Encoders page={0} controls={controls} onChange={onControlChange} />
          <Controls/>
        </ReactFlow>
        </div>
      </ReactFlowProvider>
      
    </div>
    :
    <div>         
      <div style={{position:"absolute", left:"50vw", top:"20vh"}} >GIMME PASSWORD
      <EditText style={{width:"150px", height:"20px", backgroundColor:"#B9A", border:'1px solid black',position:"absolute",  left:"0vw", top:"50px"}} 
          onSave={(obj)=>{if(obj.value === "showme"){setAuth(true);}}}
          />
          </div>
    </div>

  );
};

export default App;