import React from 'react';
import { Handle, Position } from 'react-flow-renderer';
import {objectDesc} from '../LibraryTypes'
import './node.css';

export const overdriveDesc: objectDesc = {
  name: "Overdrive",
  desc: "Overdrive distortion",
  category: "Audio Effects",
  nodeType: "overdrive",
  inPortList: [{name: "Audio in", desc: "Audio to be distorted", type: 0}],
  outPortList: [{name: "Audio out", desc: "Distorted audio", type: 0}],
  paramList: [
    {name:"Overdrive", desc:"Distortion amount", min:0, max:1, default:0},
    {name:"Tone", desc:"Tone", min:0, max:1, default:0.5},
  ]
}


export default function OverdriveNode(props) {

  const selectedCss = props.selected? "selected" : "";
  const portStyle = { width:'10px', height:'10px', background: '#555'};
  
  return (
    <>
      {
          overdriveDesc.inPortList.map((p,i)=>{
            return(
              <Handle
                key={i}
                type="target"
                position={Position.Top}
                style={portStyle}
                onConnect={(params) => console.log('handle onConnect', params)}
              />
            )
            })
      }
      <div className={`nodeStyle ${selectedCss}`}> 
        <div className={"ObjUiTitle"} style={{paddingBottom:'5px'}}>{props.data.name}</div>
        <hr className={`objSepStyle ${selectedCss}`}/> 

        {
        overdriveDesc.paramList.map((pl,i)=>{
          return(
            <div key={i} >
              <div className="ObjUiParamName" style={{width:'70px', textAlign:'right'}}>{pl.name}</div>
              <div className="ObjUiParamVal"style={{paddingLeft:'10px', width:'70px', textAlign:'left'}}>{props.data.params[i].value}</div>
            </div>
          )
        })
        }
        </div>
      {    
        overdriveDesc.outPortList.map((p,i)=>{
          return(
            <Handle
            key={i}
              type="source"
              position={Position.Bottom}
              style={portStyle}
              onConnect={(params) => console.log('handle onConnect', params)}
            />
          )
          })
      }
    </>
  );
}