import React from 'react';
import { Handle, Position } from 'react-flow-renderer';
import {objectDesc} from '../LibraryTypes'
import './node.css';

export const mixer4Desc: objectDesc = {
  name: "Mixer4",
  desc: "4 Channel mixer",
  category: "Mix",
  nodeType: "mixer4",
  inPortList: [{name: "Audio in 1", desc: "Audio ch 1", type: 0}, 
                {name: "Audio in 2 input", desc: "Audio ch 2 input", type: 0}, 
                {name: "Audio in 3 input", desc: "Audio ch 3 input", type: 0}, 
                {name: "Audio in 4 input", desc: "Audio ch 4 input", type: 0}
            ],
  outPortList: [{name: "Audio out", desc: "Audio output", type: 0}],
  paramList: [
    {name:"Ch 1 Level", desc:"Input ch 1 level", min:0, max:1, default:0.25},
    {name:"Ch 2 Level", desc:"Input ch 2 level", min:0, max:1, default:0.25},
    {name:"Ch 3 Level", desc:"Input ch 3 level", min:0, max:1, default:0.25},
    {name:"Ch 4 Level", desc:"Input ch 4 level", min:0, max:1, default:0.25},
  ]
}


export default function Mixer4Node(props) {

  const selectedCss = props.selected? "selected" : "";
  const portStyle = { width:'10px', height:'10px', background: '#555'};
  
  return (
    <>
      {
          mixer4Desc.inPortList.map((p,i)=>{
            const ci = i.toString();
            return(
              <Handle
                key={ci}
                id={ci}
                type="target"
                position={Position.Top}
                style={ {width:'10px', height:'10px', background: '#555', left:(i*35)+35}}
                onConnect={(params) => console.log('handle onConnect', params)}
              />
            )
            })
      }
      <div className={`nodeStyle ${selectedCss}`}> 
        <div className={"ObjUiTitle"} style={{paddingBottom:'5px'}}>{props.data.name}</div>
        <hr className={`objSepStyle ${selectedCss}`}/> 

        {
        mixer4Desc.paramList.map((pl,i)=>{
          return(
            <div key={i} >
              <div className="ObjUiParamName" style={{width:'70px', textAlign:'right'}}>{pl.name}</div>
              <div className="ObjUiParamVal"style={{paddingLeft:'10px', width:'70px', textAlign:'left'}}>{props.data.params[i].value}</div>
            </div>
          )
        })
        }
        </div>
      {    
        mixer4Desc.outPortList.map((p,i)=>{
          return(
            <Handle
            key={i}
              type="source"
              position={Position.Bottom}
              style={portStyle}
              onConnect={(params) => console.log('handle onConnect', params)}
            />
          )
          })
      }
    </>
  );
}