import React from 'react';
import {lib}  from '../library/Library';
import './menu.css';

 export default function LibraryLayout() {

    const onDragStart = (event: any, nodeType: any) => {
        event.dataTransfer.setData('application/reactflow', nodeType);
        event.dataTransfer.effectAllowed = 'move';
      };

  return (
    
        <div className="libraryLayout" >
            <div style={{gridColumn: '1 / 4'}} className="menuTitle" >Library</div>
            {
                lib.categories.map((category, i) => {
                    return (
                        <div key={i}>
                            <div key={i} className="menuSubtitle">{category.name}</div>
                            {
                                Object.keys(lib.objects).map((key, index) => 
                                     lib.objects[key].category === category.name ?
                                    <div key={index} className="libraryObjectContainer">
                                        <div key={index} className="dndnode" onDragStart={(event) => onDragStart(event, lib.objects[key].nodeType)} draggable>
                                            {lib.objects[key].name}
                                        </div>
                                    </div>    
                                    : null
                                )
                            }
                        </div>
                    )
                })
            }
        </div>
  );
};

