import React from 'react';
import * as FaIcons from 'react-icons/fa';
import Button from 'react-bootstrap/Button';
import './menu.css';
import LibraryLayout from './LibraryLayout';
import PropertiesLayout from './PropertiesLayout';
import {Control} from '../controls/Controls';

export interface MenuProps {
    show:false
    controls:Control[]
    selObj:any
    onParamChange:Function
    onSave:Function
    onLoad:Function
  }

interface MenuState {
    show: boolean,
    activeTab: number
}


export class Menu extends React.Component <MenuProps>{
    
    state: MenuState;

    constructor(options: MenuProps){
        super(options);
        this.state = {show: false, activeTab: 0};
    };



    setShow(show: boolean){
        this.setState({show: show});
    }

    renderActiveTab(tab: number)
    {
        this.setState({activeTab:tab});
    }

  
	render() {

		return (
            <>
            <div className='navbar'>
                <div className="menuIcon"><FaIcons.FaBars onClick = {()=>this.setShow(!this.state.show)} /></div>
                <div style={{fontFamily: "arcade"}}>&nbsp;&nbsp;h o t w i r e</div>
            </div>
            <div className={this.state.show ? 'menu_show menu' : 'menu_hide menu'}>

                <Button active={this.state.activeTab === 0 ? true : false} className="menuTabs menuTab1"  onClick={()=>this.renderActiveTab(0)} variant="outline-secondary">Library</Button>
                <Button active={this.state.activeTab === 1 ? true : false} className="menuTabs menuTab2"  onClick={()=>this.renderActiveTab(1)} variant="outline-secondary">Inspector</Button>
                <Button active={this.state.activeTab === 2 ? true : false} className="menuTabs menuTab3"  onClick={()=>this.renderActiveTab(2)} variant="outline-secondary">File</Button>
                <Button active={this.state.activeTab === 3 ? true : false} className="menuTabs menuTab4"  onClick={()=>this.renderActiveTab(3)} variant="outline-secondary">Advanced</Button>
                 
                {
                    this.state.activeTab === 1 ? 
                        <PropertiesLayout controls={this.props.controls} selObj={this.props.selObj} onParamChange={this.props.onParamChange}/>
                    :
                    this.state.activeTab === 0 ? 
                        <LibraryLayout/>
                    : 
                    this.state.activeTab === 2 ? 
                    <>
                        <div style={{gridColumn: '1 / 5'}} className="menuTitle" >File</div>
                        <Button  className="menuButton"  onClick={()=>this.props.onSave()} >Save</Button>
                        <label className="menuButton" style={{paddingTop:'6px', height:'25px'}}>
                            Load
                            <input className="menuButton" type="file" onChange={(e) => {this.props.onLoad(e)}} />                            
                        </label>
                       
                    </>
                    :
                        null
                }
    
            </div>
            </>
		);
	}
};

