import React from 'react';
import { Handle, Position } from 'react-flow-renderer';
import {objectDesc} from '../LibraryTypes'
import './node.css';

export const svfDesc: objectDesc = {
  name: "State Variable Filter",
  desc: "Resonant state-variable filter",
  category: "Audio Effects",
  nodeType: "svf",
  inPortList: [{name: "Audio in", desc: "Audio to be filtered", type: 0}],
  outPortList: [{name: "Audio out", desc: "Filtered audio", type: 0}],
  paramList: [
    {name:"Freq", desc:"Filter center frequency", min:0, max:15000, default:15000},
    {name:"Q",  desc:"Filter resonance", min:0, max:1, default:0},
    {name:"State",  desc:"LP/BP/HP mix", min:0, max:1, default:0},
  ]
}


export default function SvfNode(props) {

  const selectedCss = props.selected? "selected" : "";
  const portStyle = { width:'10px', height:'10px', background: '#555'};
  
  return (
    <>
    {
        svfDesc.inPortList.map((p,i)=>{
          return(
            <Handle
            key={i}
              type="target"
              position={Position.Top}
              style={portStyle}
              onConnect={(params) => console.log('handle onConnect', params)}
            />
          )
          })
    }
      <div className={`nodeStyle ${selectedCss}`}> 
        <div className={"ObjUiTitle"} style={{paddingBottom:'5px'}}>{props.data.name}</div>
        <hr className={`objSepStyle ${selectedCss}`}/> 

        {
        svfDesc.paramList.map((pl,i)=>{
          return(
            <div key={i} >
              <div className="ObjUiParamName" style={{width:'70px', textAlign:'right'}}>{pl.name}</div>
              <div className="ObjUiParamVal"style={{paddingLeft:'10px', width:'70px', textAlign:'left'}}>{props.data.params[i].value}</div>
            </div>
          )
        })
        }
        </div>
    {
      svfDesc.outPortList.map((p,i)=>{
        return(
          <Handle
          key={i}
            type="source"
            position={Position.Bottom}
            style={portStyle}
            onConnect={(params) => console.log('handle onConnect', params)}
          />
        )
        })
    }
    </>
  );
};