
import {objectDictionary, libCategoryDesc, library} from './LibraryTypes'

/* Import memos and descriptors from object files */
/* TODO - Auto-import all objects in a folder */
import InputNode from './nodes/InputNode';
import OutputNode from './nodes/OutputNode';
import OverdriveNode, {overdriveDesc} from './nodes/OverdriveNode';
import SvfNode, {svfDesc} from './nodes/SvfNode';
import GainNode, {gainDesc} from './nodes/GainNode';
import XfadeNode, {xfadeDesc} from './nodes/XfadeNode';
import Mixer4Node, {mixer4Desc} from './nodes/Mixer4Node';


  /* Create lists of categories */
  export const cats:libCategoryDesc[]  = [
    {
        name: "Audio Effects",
        desc: "",
    },
    {
      name: "Mix",
      desc: ""
    },
    {
      name: "Analyse",
      desc: "",
  },
];

/* List and name all the node types to pass to react-flow */
export const nodeTypes = {
    audioInNode: InputNode,
    audioOutNode: OutputNode,
    overdrive: OverdriveNode,
    svf: SvfNode,
    gain: GainNode,
    xfade: XfadeNode,
    mixer4: Mixer4Node
  };


/* Create dictionary of object descriptors, which you can lookup with node typename */
var objs = {} as objectDictionary;

/* TODO: Add these automagially */
objs['overdrive'] = overdriveDesc;
objs['svf'] = svfDesc;
objs['gain'] = gainDesc;
objs['xfade'] = xfadeDesc;
objs['mixer4'] = mixer4Desc;

export const lib:library = {
    categories: cats,
    objects: objs
}







