import React from 'react';
import {Donut} from 'react-dial-knob'
import {EditText} from 'react-edit-text';
import './controls.css'

export interface ControlProps { control: Control, callbackVal: Function, callbackName: Function, index: number}
export interface Control { name: string, value: number}
export interface PageButtonProps { text: string, state:string, onClick: Function, pos: number}
export interface EncodersProps {
    page: number,
    controls: Control[],
    onChange: Function,
  }



export class Knob extends React.Component <ControlProps>{

  xpos = ((120*(3-this.props.index))+15);

render(){

    return <><Donut
          diameter={80}
          min={0}
          max={100}
          step={1}
          value={this.props.control.value}
          theme={{
              donutColor: getComputedStyle(document.documentElement).getPropertyValue('--highlightCol'),
              bgrColor: getComputedStyle(document.documentElement).getPropertyValue('--bgGrey'),
              centerColor: getComputedStyle(document.documentElement).getPropertyValue('--bgGrey'),
            //   maxedBgrColor?: string
              centerFocusedColor: getComputedStyle(document.documentElement).getPropertyValue('--bgGrey'),
              donutThickness: 2
          }}
          style={{
            display: 'inline-block',
            position: 'fixed',
            right: this.xpos+'px',
            marginTop: '20px',
            width: '100px'
          }}
          onValueChange={(val)=>this.props.callbackVal(val)}
          ariaLabelledBy={'my-label'}
          spaceMaxFromZero={false}
        >
         <EditText className="contolsEditText" style={{width:"100px", height:"10px"}} 
                    value={this.props.control.name} 
                     onChange={(text)=>this.props.callbackName(text)} 
                     onSave={(obj)=>this.props.callbackName(obj.value)}
                    />
      </Donut>
      </>
  }
}


export class PageButton extends React.Component <PageButtonProps>{

  render(){
          return <div style={{right:this.props.pos+'px', 
                        borderColor:this.props.state==="inactive"?'var(--unHighlightCol)':'var(--highlightCol)',
                        borderWidth:this.props.state==="selected"?'3px':'1px'
                      }} 
                      className="pageButton" onClick={()=>this.props.onClick()}>{this.props.text}</div>
    }
  }


export class Encoders extends React.Component <EncodersProps>{

  state = {page:0};

    constructor(props) {
        super(props);
        this.state = {page: 0};
      }

    addPage(pageNumber: number){
      if(this.props.controls.length / 4 <= pageNumber){
        let ctls = [...this.props.controls];
        ctls.push({name:"ctl"+ctls.length.toString(), value:0});
        ctls.push({name:"ctl"+ctls.length.toString(), value:0});
        ctls.push({name:"ctl"+ctls.length.toString(), value:0});
        ctls.push({name:"ctl"+ctls.length.toString(), value:0});
        this.props.onChange(ctls, null, null, null);
      }
    }

    setPage(pageNumber: number){
      this.setState({page: pageNumber});
    }

    render(){

      var knobs: JSX.Element[] = [];
      var pageButtons: JSX.Element[] = [];
      var pos = 0;

      for (var i = 0; i < 4; i++) {
        const cont = i;
        knobs.push(<Knob key={cont} control={this.props.controls[(this.state.page*4)+cont]} 
                        callbackVal={(val)=>{this.props.onChange(null, (this.state.page*4)+cont, val, null);}} 
                        callbackName={(name)=>{this.props.onChange(null, (this.state.page*4)+cont, null, name);}} 
                        index={cont} />)
      }

      for (i = 0; i < 8; i++) {
        const p = i;

        pos = ((7-i)*62)-7;
        let lastIdx = (i+1)*4;

        if(this.props.controls.length >= lastIdx){
          pageButtons.push(<PageButton key={i} pos={pos} text={i.toString()} state={this.state.page===p?"selected":"active"} onClick={()=>{this.setPage(p)}}/>);
        }else{
          if( lastIdx === this.props.controls.length+4){
            pageButtons.push(<PageButton key={i} pos={pos} text="+" state="active" onClick={()=>{this.addPage(p)}}/>);
          }else{
            pageButtons.push(<PageButton key={i} pos={pos} text="" state="inactive" onClick={()=>{}}/>);
          }
        }
      }

        return(
            <div className="controlPane">
              {knobs}
              <div className="pages">
                {pageButtons}
              </div>
            </div>
        )
    }
}

