import React from 'react';
import { Handle, Position } from 'react-flow-renderer';
import {objectDesc} from '../LibraryTypes'
import './node.css';

export const xfadeDesc: objectDesc = {
  name: "Xfade",
  desc: "Crossfade 2-ch mixer",
  category: "Mix",
  nodeType: "xfade",
  inPortList: [{name: "Audio in 1", desc: "Audio ch 1", type: 0}, {name: "Audio in 2 input", desc: "Audio ch 2 input", type: 0}],
  outPortList: [{name: "Audio out", desc: "Audio output", type: 0}],
  paramList: [
    {name:"Mix", desc:"Fade between ch1 and ch2", min:0, max:1, default:0.5},
  ]
}


export default function XfadeNode(props) {

  const selectedCss = props.selected? "selected" : "";
  const portStyle = { width:'10px', height:'10px', background: '#555'};
  
  return (
    <>
      {
          xfadeDesc.inPortList.map((p,i)=>{
            const ci = i.toString();
            return(
              <Handle
                key={ci}
                id={ci}
                type="target"
                position={Position.Top}
                style={ {width:'10px', height:'10px', background: '#555', left:(i*60)+60}}
                onConnect={(params) => console.log('handle onConnect', params)}
              />
            )
            })
      }
      <div className={`nodeStyle ${selectedCss}`}> 
        <div className={"ObjUiTitle"} style={{paddingBottom:'5px'}}>{props.data.name}</div>
        <hr className={`objSepStyle ${selectedCss}`}/> 

        {
        xfadeDesc.paramList.map((pl,i)=>{
          return(
            <div key={i} >
              <div className="ObjUiParamName" style={{width:'70px', textAlign:'right'}}>{pl.name}</div>
              <div className="ObjUiParamVal"style={{paddingLeft:'10px', width:'70px', textAlign:'left'}}>{props.data.params[i].value}</div>
            </div>
          )
        })
        }
        </div>
      {    
        xfadeDesc.outPortList.map((p,i)=>{
          return(
            <Handle
            key={i}
              type="source"
              position={Position.Bottom}
              style={portStyle}
              onConnect={(params) => console.log('handle onConnect', params)}
            />
          )
          })
      }
    </>
  );
}