import React from 'react';
import NumericInput from 'react-numeric-input';
import Select from 'react-select'
import {lib} from '../library/Library'
import './menu.css';

const customStyles = {
  menu: (provided, state) => ({
    ...provided,
    backgroundColor: 'var(--bgGrey)',
    color: 'whitesmoke',
  }),
  control: (provided, state) => ({
    ...provided,
    backgroundColor: state.isSelected?'var(--bgGrey2)':'var(--bgGrey)',
    color: 'whitesmoke',
    width: '150px',
  }),
  option: (provided, state) => ({
    ...provided,
    backgroundColor: state.isSelected ?'var(--bgGrey2)':'var(--bgGrey)',
    color: 'whitesmoke',
    ':hover': {
      backgroundColor: 'var(--bgGrey2)'
    }
  }),
  singleValue: (provided, state) => ({
    ...provided,
    backgroundColor: state.isSelected?'var(--bgGrey2)':'var(--bgGrey)',
    color: 'whitesmoke',
  })
}


export default function PropertiesLayout(props) {
    
  var opts=[{value:999, label:"Fixed"}];
  var shapeOpts=[{value:0, label:"Linear"}, {value:1, label:"Exp"}, {value:2, label:"Para"}];
  const numboxStyle = {input: {height: '35px', padding:'10px', color: 'var(--accentCol)', fontSize: '16px', background: 'var(--bgGrey)'}};

  var paramsCpy:any = null;
  
  if(props.selObj?.data?.params.length > 0){
    paramsCpy = [...props.selObj?.data?.params];
  }

  props.controls.forEach((c, i) => {
      opts.push({value: i, label: c.name});
  })

  return (
     
    <>
        <div className="libraryLayout">
            <div className="menuTitle">Properties</div>
                {
                  (paramsCpy!=null)?      

                  lib.objects[props.selObj.type].paramList.map((c, i) => {
                    return(
                      <>
                        <div className="inspectorSubTitle">{c.name}</div>
                        <div className="inspectorProp">
                          <div className="inspectorLabel">Control:</div> 
                          <Select styles={customStyles} options={opts} value={opts[props.selObj.data.params[i].fixed?0:(props.selObj.data.params[i].ctlIdx)+1]}
                                onChange={(opt)=>{
                                  if(opt!.label === "Fixed"){
                                    paramsCpy[i].fixed = true;
                                  }else{
                                    paramsCpy[i].fixed = false;
                                    paramsCpy[i].ctlIdx = opt?.value;
                                  }
                                  props.onParamChange(paramsCpy);
                                }}/>
                        </div>
                          
                          {props.selObj.data.params[i].fixed?
                          <div className="inspectorProp">
                              <div className="inspectorLabel">Value:</div> 
                              <NumericInput min={c.min} max={c.max} value={props.selObj.data.params[i].value} step={0.1} precision={2} size={5} style={numboxStyle}
                              onChange={(val)=>{
                                    if(val!=null){
                                      paramsCpy[i].value=val;
                                      props.onParamChange(paramsCpy);
                                    }}}/>
                           </div>
                            :
                            <>
                            <div className="inspectorProp">
                              <div className="inspectorLabel">Min:</div>
                              <NumericInput min={c.min} max={c.max} value={props.selObj.data.params[i].mapMin} step={0.1} precision={2} size={5} style={numboxStyle}
                              onChange={(val)=>{
                                  if(val!=null){
                                      paramsCpy[i].mapMin=val;
                                      props.onParamChange(paramsCpy);
                                    }}}/>
                            </div> 
                            <div className="inspectorProp">
                              <div className="inspectorLabel">Max:</div>
                                <NumericInput min={c.min} max={c.max} value={props.selObj.data.params[i].mapMax} step={0.1} precision={2} size={5} style={numboxStyle}
                                onChange={(val)=>{
                                    if(val!=null){
                                        paramsCpy[i].mapMax=val;
                                        props.onParamChange(paramsCpy);
                                      }}}/>
                            </div>
                            <div className="inspectorProp">
                              <div className="inspectorLabel">Shape:</div>
                                <Select styles={customStyles} options={shapeOpts} value={shapeOpts[props.selObj.data.params[i].mapShape]}
                                    onChange={(opt)=>{
                                      paramsCpy[i].mapShape = opt?.value;
                                        props.onParamChange(paramsCpy);
                                    }}/>
                            </div>
                            </>
                          }
                        
                      </>
                    )

                  })  
                  : <div>Select a single object</div>

                }

        </div>
    </>
  );

};

